	module.exports = {"pathContext":{"docHtml":"<h1>2.0.2 / 2017-09-24</h1>\n<ul>\n<li>\n<p>deps: forwarded@~0.1.2</p>\n<ul>\n<li>perf: improve header parsing</li>\n<li>perf: reduce overhead when no <code>X-Forwarded-For</code> header</li>\n</ul>\n</li>\n</ul>\n<h1>2.0.1 / 2017-09-10</h1>\n<ul>\n<li>\n<p>deps: forwarded@~0.1.1</p>\n<ul>\n<li>Fix trimming leading / trailing OWS</li>\n<li>perf: hoist regular expression</li>\n</ul>\n</li>\n<li>deps: ipaddr.js@1.5.2</li>\n</ul>\n<h1>2.0.0 / 2017-08-08</h1>\n<ul>\n<li>Drop support for Node.js below 0.10</li>\n</ul>\n<h1>1.1.5 / 2017-07-25</h1>\n<ul>\n<li>Fix array argument being altered</li>\n<li>deps: ipaddr.js@1.4.0</li>\n</ul>\n<h1>1.1.4 / 2017-03-24</h1>\n<ul>\n<li>deps: ipaddr.js@1.3.0</li>\n</ul>\n<h1>1.1.3 / 2017-01-14</h1>\n<ul>\n<li>deps: ipaddr.js@1.2.0</li>\n</ul>\n<h1>1.1.2 / 2016-05-29</h1>\n<ul>\n<li>\n<p>deps: ipaddr.js@1.1.1</p>\n<ul>\n<li>Fix IPv6-mapped IPv4 validation edge cases</li>\n</ul>\n</li>\n</ul>\n<h1>1.1.1 / 2016-05-03</h1>\n<ul>\n<li>Fix regression matching mixed versions against multiple subnets</li>\n</ul>\n<h1>1.1.0 / 2016-05-01</h1>\n<ul>\n<li>\n<p>Fix accepting various invalid netmasks</p>\n<ul>\n<li>IPv4 netmasks must be contingous</li>\n<li>IPv6 addresses cannot be used as a netmask</li>\n</ul>\n</li>\n<li>deps: ipaddr.js@1.1.0</li>\n</ul>\n<h1>1.0.10 / 2015-12-09</h1>\n<ul>\n<li>\n<p>deps: ipaddr.js@1.0.5</p>\n<ul>\n<li>Fix regression in <code>isValid</code> with non-string arguments</li>\n</ul>\n</li>\n</ul>\n<h1>1.0.9 / 2015-12-01</h1>\n<ul>\n<li>\n<p>deps: ipaddr.js@1.0.4</p>\n<ul>\n<li>Fix accepting some invalid IPv6 addresses</li>\n<li>Reject CIDRs with negative or overlong masks</li>\n</ul>\n</li>\n<li>perf: enable strict mode</li>\n</ul>\n<h1>1.0.8 / 2015-05-10</h1>\n<ul>\n<li>deps: ipaddr.js@1.0.1</li>\n</ul>\n<h1>1.0.7 / 2015-03-16</h1>\n<ul>\n<li>\n<p>deps: ipaddr.js@0.1.9</p>\n<ul>\n<li>Fix OOM on certain inputs to <code>isValid</code></li>\n</ul>\n</li>\n</ul>\n<h1>1.0.6 / 2015-02-01</h1>\n<ul>\n<li>deps: ipaddr.js@0.1.8</li>\n</ul>\n<h1>1.0.5 / 2015-01-08</h1>\n<ul>\n<li>deps: ipaddr.js@0.1.6</li>\n</ul>\n<h1>1.0.4 / 2014-11-23</h1>\n<ul>\n<li>\n<p>deps: ipaddr.js@0.1.5</p>\n<ul>\n<li>Fix edge cases with <code>isValid</code></li>\n</ul>\n</li>\n</ul>\n<h1>1.0.3 / 2014-09-21</h1>\n<ul>\n<li>Use <code>forwarded</code> npm module</li>\n</ul>\n<h1>1.0.2 / 2014-09-18</h1>\n<ul>\n<li>Fix a global leak when multiple subnets are trusted</li>\n<li>Support Node.js 0.6</li>\n<li>deps: ipaddr.js@0.1.3</li>\n</ul>\n<h1>1.0.1 / 2014-06-03</h1>\n<ul>\n<li>Fix links in npm package</li>\n</ul>\n<h1>1.0.0 / 2014-05-08</h1>\n<ul>\n<li>\n<p>Add <code>trust</code> argument to determine proxy trust on</p>\n<ul>\n<li>Accepts custom function</li>\n<li>Accepts IPv4/IPv6 address(es)</li>\n<li>Accepts subnets</li>\n<li>Accepts pre-defined names</li>\n</ul>\n</li>\n<li>Add optional <code>trust</code> argument to <code>proxyaddr.all</code> to\nstop at first untrusted</li>\n<li>Add <code>proxyaddr.compile</code> to pre-compile <code>trust</code> function\nto make subsequent calls faster</li>\n</ul>\n<h1>0.0.1 / 2014-05-04</h1>\n<ul>\n<li>Fix bad npm publish</li>\n</ul>\n<h1>0.0.0 / 2014-05-04</h1>\n<ul>\n<li>Initial release</li>\n</ul>","docPath":"argo-ci/node_modules/proxy-addr/history","proj":"argo-ci"}}

